import { useState , useEffect, useRef } from 'react';
import Nav from './components/nav';
import './style/index.css';
import Hls from 'hls.js';




function App() {
  let [showPlayer , setShowPlayer] = useState("cards");
  let [videoSrc , setVideoSrc] = useState();
  let [filetype , setFileType] = useState();
  let [data , setData] = useState([]);

  useEffect(()=>{
    const json =  fetch("config.json").then(r => r.json())
    json.then(jsonData=>{setData(jsonData)})
  },[])

  useEffect(()=>{
    console.log(showPlayer)
  },[showPlayer])

function handlePlayer(action,showType) {
  if("play" === action){
    setShowPlayer(showType)
  }else if("exit" === action){
    setShowPlayer("cards");
  }
}

function playFilm(link,type){
  setVideoSrc(link);
  setFileType(type);
    handlePlayer("play",type);
}

//To run mp4

function Playermp4(){
  return(
      <div>
      <video controls src={videoSrc}></video>
      <button className="btn btn-danger w-100" onClick={()=>{handlePlayer("exit")}}>Exit</button>
      </div>
  );
}

//To run m3u8
function Playerm3u8(){
  const videoRef = useRef(null);
  switch(filetype){
    case 'mp4':
      
    ;break;
    case 'm3u8':
      
    ;break;
    default:;break;
  }
  useEffect(() => {
      if (Hls.isSupported()) {
          const hls = new Hls();
          hls.loadSource(videoSrc);
          hls.attachMedia(videoRef.current);
          hls.on(Hls.Events.MANIFEST_PARSED, () => {
              videoRef.current.play();
          });
      } else if (videoRef.current.canPlayType('application/vnd.apple.mpegurl')) {
          videoRef.current.src = videoSrc;
          videoRef.current.addEventListener('loadedmetadata', () => {
              videoRef.current.play();
          });
      }
  }, [videoSrc]);

  return(
      <div>
      <video controls ref={videoRef}></video>
      <button className="btn btn-danger w-100" onClick={()=>{handlePlayer("exit")}}>Exit</button>
      </div>
  );
}



  const cards = data.map((film) => (
    <button type="button" className="btn btn-light" onClick={() => playFilm(film.streamLink,film.fileType)}>
      {film.name}
    </button>
  ));

  return (
    <div className='container'>
      <Nav />
      <div className='cards-group'>
      {showPlayer === "cards"? cards: showPlayer === "mp4"? <Playermp4/> :showPlayer === "m3u8"? <Playerm3u8/> : <></>  }
      </div>
      
    </div>
  );
}

export default App;
